import React, { useEffect, useState } from 'react';
import { BrowserRouter } from 'react-router-dom';
import './App.css';
import './Styles/Header.css';
import Header from './Components/Header';
import Routing from './Routing';

function App() {
  const [scrollDirection, setScrollDirection] = useState('none');//   
  const [prevScrollY, setPrevScrollY] = useState(0); //    

  useEffect(() => {
    //    
    const handleScroll = () => {
      const currentScrollY = window.scrollY;

      //if ( top : 0 none.)      
      //else if(          down)
      //else(  up .)
      //log  
      if (currentScrollY === 0) {
        setScrollDirection('none');
        console.log("none");
      } else if (currentScrollY > prevScrollY) {
        if (scrollDirection !== 'down') {
          setScrollDirection('down');
          console.log("down");
        }
      } else {
        if (scrollDirection !== 'up') {
          setScrollDirection('up');
          console.log("up");
        }
      }

      //    prevScrollY  
      setPrevScrollY(currentScrollY);
    };

    window.addEventListener('scroll', handleScroll);//addEventListnener     

    //      
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [scrollDirection, prevScrollY]); // scrollDirection prevScrollY   

  //     
  //down  scroll-down    up
  const headerClasses = `header ${scrollDirection === 'down' ? 'scroll-down' : 'scroll-up'}`;

  return (
    <BrowserRouter>
      <div className="App">
        {/* Header props headerClasses    */}
        <Header headerClassName={headerClasses} />
        <Routing />
      </div>
    </BrowserRouter>
  );
}

export default App;
